<template>
    <div class="review page">
        <h1>Review your answers</h1>
        <div class="question" v-for="(question, index) in getSelectedSet" :key="question._id">
            <h3>{{ index + 1 }}. {{ question.text }}</h3>
            <ol>
                <li
                    v-for="choice in question.choices"
                    :key="choice.text"
                    v-text="choice.text"
                    :class="choice.chosen && choice.correct ? 'correct' : choice.chosen && !choice.correct ? 'wrong' : ''"
                ></li>
            </ol>
        </div>
        <router-link to="/">
            <button class="route-btn" @click="reset()">Back to home</button>
        </router-link>
    </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Review",
  computed: mapGetters(["getSelectedSet", "correctQuestions"])
};
</script>

<style lang="sass">
.review
    h1
        margin-bottom: 2rem
    .question
        margin-bottom: 1rem
        border-bottom: 1px solid white
        ol
            list-style: lower-alpha
            li
                margin-bottom: 1rem
                &.correct
                    color: #21bf73
                &.wrong
                    color: red
</style>
