<template>
    <div class="progress-container">
        <div class="progress-bar" :style="`width: ${getProgress + 1}%`"></div>
        <template v-if="getSelectedSet.length !== 0">
          {{ getSelectedQuestionIndex + 1 }}/{{ getSelectedSet.length }}
        </template>
        <template v-else>
          {{ getSelectedQuestionIndex + 1 }}/{{ allQuestions.length }}
        </template>
    </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "ProgressBar",
  computed: mapGetters([
    "allQuestions",
    "getSelectedSet",
    "getSelectedQuestionIndex",
    "getProgress"
  ])
};
</script>

<style lang="sass">
.progress-container
  background: #333
  position: fixed
  top: 0
  width: 100%
  height: 1rem
  text-align: center
  .progress-bar
    position: absolute
    z-index: -1
    top: 0
    left: 0
    width: 0%
    height: 100%
    background: #21bf73
    transition: width .5s linear
</style>
